<template>
  <div class="app-container">
    <MainView 
      v-if="!isCardView" 
      :rulesCardSrc="rulesCardSrc"
      @start-game="startCardView"
    />
    
    <CardView 
      v-else 
      :cardBackSrc="cardBackSrc"
      :excludedCards="excludedCards"
      @return-to-main="returnToMain"
    />
  </div>
</template>

<script>
import MainView from './components/MainView.vue';
import CardView from './components/CardView.vue';

export default {
  name: 'App',
  components: {
    MainView,
    CardView
  },
  data() {
    return {
      isCardView: false,
      rulesCardSrc: 'https://s2.loli.net/2025/04/26/R5nBQ9hqZKGf6jI.jpg',
      cardBackSrc: 'https://s2.loli.net/2025/04/26/a2wjXmz8En6HNRC.jpg',
      excludedCards: []
    }
  },
  methods: {
    // 开始抽卡视图
    startCardView(excludedCards = []) {
      this.excludedCards = excludedCards;
      this.isCardView = true;
    },
    
    // 返回主界面
    returnToMain() {
      this.isCardView = false;
      this.excludedCards = [];
    }
  }
}
</script>

